        img(src='@/assets/curso/temas/img02.svg' style="max-width: 32px; margin-left: -16px;")
        h3.col.ps-3.mb-0 Mtodos de suministro por especie y condicin

    p.mb-5 Las tcnicas de suministro varan segn la especie y si esta se transporta suelta o en unidades de contencin.

    AcordionA.color-acento-botones.mb-5(tipo="a" clase-tarjeta="tarjeta BG03" data-aos="zoom-in")
      .row.justify-content-center(titulo="Suministro en especies mayores (bovinos, ovinos, porcinos)")
        .col-lg-6.order-lg-1.order-2
          p #[strong Tcnica de desembarque controlado]: en muchos casos, el mtodo ms efectivo es desembarcar controladamente los animales en un corral o manga del lugar de descanso que est equipado con bebederos y comederos accesibles y seguros.
          p.mb-0 #[strong Tcnica de acceso en vehculo]: si el desembarque no es posible, se debe utilizar un mtodo para hacer accesible el agua y el alimento dentro del vehculo, asegurando que los utensilios de suministro no representen un riesgo de lesin.
        .col-lg-6.col-10.order-lg-2.order-1.mb-lg-0.mb-4
          figure
            img(src='@/assets/curso/tema3/img18.jpg', alt="Imagen decorativa")
      .row.justify-content-center(titulo="Suministro en especies contenidas (aves)")
        .col-lg-6.order-lg-1.order-2
          p.mb-0 #[strong Tcnica de contencin suficiente]: para especies frgiles como los pollitos de un da de edad, la tcnica de manejo es asegurar que las cajas utilizadas contengan agua y alimento suficiente para la duracin del trayecto. El suministro se realiza antes del cargue, dentro de la propia unidad de contencin.
        .col-lg-6.col-10.order-lg-2.order-1.mb-lg-0.mb-4
          figure
            img(src='@/assets/curso/tema3/img19.jpg', alt="Imagen decorativa")

    .row.justify-content-center.mb-4  
      .col-lg-10(data-aos="fade-right")
        .cajon.color-primario.p-4
          p.mb-0 #[strong Nota]: la aplicacin del mtodo debe asegurar la calidad y la cantidad de agua y alimento, y debe ejecutarse con calma, siguiendo los principios de manejo etolgico para evitar que el estrs en la parada inhiba el consumo.

    separador
    #t_3_4.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 3.4 Tcnicas de conduccin en transporte de animales
    
    .row.justify-content-center.mb-5
      .col-lg-3.col-6.mb-lg-0.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/tema3/img20.png' alt="Imagen decorativa")
      .col-lg-9(data-aos="fade-left")
        p.mb-5 Las tcnicas de conduccin son un conjunto de procedimientos operacionales que debe aplicar el conductor para minimizar el estrs, el miedo y las lesiones de los animales en movimiento. La conduccin es un factor crtico, ya que los movimientos bruscos (aceleracin y frenado) son una de las principales causas de cadas y traumatismos en la carga viva.
        .mb-4.d-flex
          .d-flex.flex-wrap.align-items-center.BG04.pe-4(style="border-radius: 5px;")
            img(src='@/assets/curso/temas/img02.svg' style="max-width: 32px; margin-left: -16px;")
            h3.col.ps-3.mb-0 Principio fundamental: conduccin suave
        p La tcnica de conduccin se rige por el principio de evitar los movimientos bruscos que alteran el equilibrio esttico de los animales.
        ul.lista-ul--color
          li(style="margin-bottom: 2px")
            i.fas.fa-horse-head.tcp
            | Evitar cambios bruscos: el conductor tiene la obligacin de evitar la aceleracin y el frenado bruscos, las maniobras violentas y las curvas tomadas a alta velocidad.
          li(style="margin-bottom: 2px")
            i.fas.fa-horse-head.tcp
            | Adaptacin a la carga: la tcnica exige conducir adaptando la velocidad y el estilo al hecho de que se transporta una carga viva e inestable, siendo ms precavido que con la carga inanimada.

    .row.justify-content-center.mb-5
      .col-lg-10(data-aos="fade-right")
        .cajon.color-secundario.p-4
          p.mb-0 #[strong Justificacin etolgica]: las fuerzas de inercia y centrfugas generadas por la conduccin brusca obligan al animal a hacer un esfuerzo constante para mantener el equilibrio esttico, provocando fatiga, estrs y cadas que pueden resultar en lesiones graves como fracturas.

    .mb-4.d-flex(data-aos="fade-right")
      .d-flex.flex-wrap.align-items-center.BG04.pe-4(style="border-radius: 5px;")
        img(src='@/assets/curso/temas/img02.svg' style="max-width: 32px; margin-left: -16px;")
        h3.col.ps-3.mb-0 Tcnicas de conduccin relacionadas con el bienestar
    
    p.mb-5 A continuacin, se presenta el recurso que relaciona las tcnicas de conduccin con su impacto directo en el bienestar animal y el soporte normativo correspondiente.

    .titulo-sexto.color-acento-contenido(data-aos="fade-right")
      h5 Tabla 19.
      i Tcnicas de conduccin relacionadas con el bienestar
