            td(style="background-color: #F6F6F6;") Descanso obligatorio ocho horas despus de ocho horas de conduccin. Manual de procedimiento (nm. 4.i).

    .row.justify-content-center.mb-5
      .col-lg-10
        .mb-4.d-flex(data-aos="fade-right")
          .d-flex.flex-wrap.align-items-center.BG04.pe-4(style="border-radius: 5px;")
            img(src='@/assets/curso/temas/img02.svg' style="max-width: 32px; margin-left: -16px;")
            h3.col.ps-3.mb-0 Tcnicas de conduccin en contingencias
        p.mb-0 En caso de accidente, avera u otra situacin imprevista, la tcnica de conduccin se transforma en un protocolo de emergencia con nfasis en la proteccin inmediata de la carga viva.

    .row.justify-content-center.mb-4
      .col-lg-6.mb-4(data-aos="fade-right")
        .tarjeta-avatar-b.h-100
          .tarjeta-avatar-b__img
            img(src='@/assets/curso/tema3/img21.svg' alt="Imagen decorativa")
          .tarjeta.BG03
            .p-4
              .h4 Parada segura
              p.mb-0 Si ocurre un evento, la tcnica exige detener el vehculo en un lugar seguro y, de ser posible, con sombra, para reducir el impacto trmico y permitir la aplicacin inmediata del plan de contingencia.
      .col-lg-6.mb-4(data-aos="fade-left")
        .tarjeta-avatar-b.h-100
          .tarjeta-avatar-b__img
            img(src='@/assets/curso/tema3/img22.svg' alt="Imagen decorativa")
          .tarjeta.BG03
            .p-4
              .h4 Notificacin y monitoreo
              p.mb-0 El conductor es responsable de notificar a la autoridad sanitaria competente (ICA) y de atender de forma inmediata a los animales lesionados o cados, siguiendo los procedimientos establecidos.

    separador
    #t_3_5.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 3.5 Tcnicas de inspeccin
    
    p.mb-5 Las tcnicas de inspeccin son el conjunto de procedimientos de vigilancia activados que deben ser ejecutados por el conductor o tripulante durante el viaje. El objetivo es monitorear el bienestar de los animales y verificar las condiciones estructurales y ambientales del vehculo, asegurando que se cumplen los requisitos normativos y se detectan las emergencias a tiempo.

    .row.justify-content-center.mb-5
      .col-lg-10
        .mb-4.d-flex(data-aos="fade-right")
          .d-flex.flex-wrap.align-items-center.BG04.pe-4(style="border-radius: 5px;")
            img(src='@/assets/curso/temas/img02.svg' style="max-width: 32px; margin-left: -16px;")
            h3.col.ps-3.mb-0 Frecuencia y responsabilidad de la inspeccin
        
        p.mb-5 La tcnica de inspeccin es una obligacin operativa de la tripulacin con una periodicidad definida. A continuacin, se presenta el recurso que organiza los aspectos clave de esta tcnica.

        .tarjeta--container.row
          .col-md.tarjeta.color-primario.p-5(data-aos="fade-right")
            .row.justify-content-center.mb-4
              .col-6
                figure
                  img(src='@/assets/curso/tema3/img23.svg', alt="Imagen decorativa")
            h4.text-center Responsable
            p El conductor o tripulante es el responsable de ejecutar la inspeccin y el monitoreo constante.
            .h4 Soporte documental (texto exacto)
            p.mb-0 El conductor o tripulante deber realizar la inspeccin  Manual de procedimiento (nm. 4.g).
          .col-md.tarjeta.color-secundario.p-5(data-aos="fade-left")
            .row.justify-content-center.mb-4
              .col-6
                figure
                  img(src='@/assets/curso/tema3/img24.svg', alt="Imagen decorativa")
            h4.text-center Periodicidad
            p La inspeccin debe realizarse, por lo menos, cada cuatro (4) horas durante el viaje (inspeccin en movimiento).
            .h4 Soporte documental (texto exacto)
            p.mb-0 por lo menos cada cuatro (4) horas durante el viaje.  Manual de procedimiento (nm. 4.g).

    .mb-4.d-flex(data-aos="fade-right")
      .d-flex.flex-wrap.align-items-center.BG04.pe-4(style="border-radius: 5px;")
        img(src='@/assets/curso/temas/img02.svg' style="max-width: 32px; margin-left: -16px;")
        h3.col.ps-3.mb-0 Tipos de inspeccin y puntos de control
    
    p.mb-5 La tcnica de inspeccin debe cubrir dos reas principales: la carga viva (los animales) y la unidad de transporte (el vehculo y su ambiente).

    AcordionA.color-acento-botones.mb-5(tipo="a" clase-tarjeta="tarjeta BG03" data-aos="zoom-in")
      .row.justify-content-center(titulo="Inspeccin de la carga viva (bienestar animal)")
        .col-lg-6.order-lg-1.order-2
          p El conductor o tripulante debe aplicar la tcnica de observacin para detectar problemas de salud o manejo.
          ul.lista-ul--color.mb-0
            li(style="margin-bottom: 2px")
              i.fas.fa-check-circle
              p.mb-0 #[strong Deteccin de cadas y lesiones]: buscar animales que estn cados, que no puedan levantarse (prdida del equilibrio esttico), o que presenten signos de lesiones o enfermedad.
            li(style="margin-bottom: 2px")
              i.fas.fa-check-circle
              p.mb-0 #[strong Signos de estrs trmico]: observar signos de estrs por calor (jadeo excesivo, babeo) o estrs por fro (temblores).
            li(style="margin-bottom: 2px")
              i.fas.fa-check-circle
              p.mb-0 #[strong Comportamiento del grupo]: vigilar si hay peleas o hostigamiento entre animales, lo que indica fallas en las divisiones o en la separacin.
        .col-lg-6.col-10.order-lg-2.order-1.mb-lg-0.mb-4
          figure
            img(src='@/assets/curso/tema3/img25.jpg', alt="Imagen decorativa")
      .row.justify-content-center(titulo="Inspeccin de la unidad de transporte (estructural y ambiental)")
        .col-lg-6.order-lg-1.order-2
          p El personal debe comprobar la infraestructura y el microclima del vehculo.
          ul.lista-ul--color.mb-0
            li(style="margin-bottom: 2px")
              i.fas.fa-check-circle
              p.mb-0 #[strong Control de la ventilacin y temperatura]: verificar que la ventilacin sea constante y que la temperatura dentro del vehculo se mantenga dentro del rango aceptable (entre 5 C y 30 C).
            li(style="margin-bottom: 2px")
              i.fas.fa-check-circle
              p.mb-0 #[strong Integridad estructural]: revisar que las divisiones o barreras fuertes utilizadas para la separacin estn firmemente aseguradas.
            li(style="margin-bottom: 2px")
              i.fas.fa-check-circle
              p.mb-0 #[strong Bioseguridad]: inspeccionar que el sistema de contencin de excretas contine evitando la filtracin de orina y heces hacia la va pblica.
        .col-lg-6.col-10.order-lg-2.order-1.mb-lg-0.mb-4
          figure
            img(src='@/assets/curso/tema3/img26.jpg', alt="Imagen decorativa")

    .row.justify-content-center.mb-5
      .col-lg-10
        .cajon.color-secundario.p-4
          p.mb-0 #[strong Soporte documental]: la inspeccin debe incluir el control de la ventilacin constante y el control de la temperatura.  Manual de procedimiento (nm. 4.f).

    .mb-4.d-flex(data-aos="fade-right")
      .d-flex.flex-wrap.align-items-center.BG04.pe-4(style="border-radius: 5px;")
        img(src='@/assets/curso/temas/img02.svg' style="max-width: 32px; margin-left: -16px;")
        h3.col.ps-3.mb-0 Tcnicas de inspeccin en paradas largas (atencin adicional)
    
    p.mb-5 Cuando la inspeccin coincide con una interrupcin prolongada del viaje (parada de descanso y atencin), la tcnica de inspeccin se ampla para incluir acciones de manejo directo sobre la carga.

    .BGM12.px-md-5.px-4
      .row.justify-content-center.mb-4
        .col-lg-10
          ol.lista-ol--cuadro.lista-ol--separador.mb-5
            li(data-aos="fade-left") 
              .lista-ol--cuadro__vineta
                span 1
              p.mb-0 Acomodacin: inspeccionar y reajustar la posicin de los animales que se hayan desplazado o quedado en posiciones incmodas.
            li(data-aos="fade-left") 
              .lista-ol--cuadro__vineta
                span 2
              p.mb-0 Hidratacin y alimentacin: si el plan de ruta lo exige, la inspeccin debe constatar que se est realizando la provisin de agua y alimento.
          .cajon.color-primario.p-4
            p.mb-0 #[strong Soporte documental (accin en la parada)]: la parada se realiza para inspeccin, acomodacin, hidratacin o alimentacin.  Manual de procedimiento (nm. 4.h).

    separador
    #t_3_6.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 3.6 Tcnicas de supervisin de animales en trayecto 
    
    .row.justify-content-center.mb-5
      .col-lg-5.col-10.mb-lg-0.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/tema3/img27.jpg' alt="Imagen decorativa")
      .col-lg-7(data-aos="fade-left")
        p Las tcnicas de supervisin de animales en trayecto constituyen el conjunto de acciones continuas de vigilancia activa que deben ser ejecutadas por el conductor o tripulante. El objetivo es garantizar el bienestar de la carga viva, el cumplimiento de la normativa y la deteccin temprana de cualquier situacin de emergencia o riesgo durante el movimiento.
        .mb-4.d-flex
          .d-flex.flex-wrap.align-items-center.BG04.pe-4(style="border-radius: 5px;")
            img(src='@/assets/curso/temas/img02.svg' style="max-width: 32px; margin-left: -16px;")
            h3.col.ps-3.mb-0 Tcnica de monitoreo activo (inspeccin rutinaria)
        p La tcnica de supervisin se rige por una periodicidad obligatoria, siendo una de las responsabilidades principales de la tripulacin en ruta.
        p.mb-0 A continuacin, se presenta el recurso que describe la periodicidad y el alcance obligatorio de la supervisin en trayecto.

    .tarjeta.BG08.p-4.mb-5(data-aos="fade-right")
      SlyderA.color-acento-botones(tipo="b" data-aos="zoom-in")
        .row.justify-content-center.align-items-center
          .col-lg-6.order-lg-1.order-2
            .h4 Frecuencia obligatoria
            p.mb-5 La supervisin debe realizarse por lo menos cada cuatro (4) horas durante todo el viaje.
            .h4 Soporte documental (texto exacto)
            p.mb-0 "El conductor o tripulante deber realizar la inspeccin... por lo menos cada cuatro (4) horas durante el viaje."  Manual de procedimiento (Num. 4.g)
          .col-lg-6.col-10.order-lg-2.order-1.mb-lg-0.mb-4
            img(src='@/assets/curso/tema3/img28.jpg' alt="Imagen decorativa")
        .row.justify-content-center.align-items-center
          .col-lg-6.order-lg-1.order-2
            .h4 Alcance de la supervisin
            p.mb-5 La tcnica exige supervisar tanto la unidad de transporte (estructural y ambiental) como los animales en movimiento (su condicin fsica y social).
            .h4 Soporte documental (texto exacto)
            p.mb-0 "...inspeccin de la unidad de transporte de animales y de los animales en movimiento..."  Manual de procedimiento (Num. 4.g)
          .col-lg-6.col-10.order-lg-2.order-1.mb-lg-0.mb-4
            img(src='@/assets/curso/tema3/img29.jpg' alt="Imagen decorativa")

    .mb-4.d-flex(data-aos="fade-right")
      .d-flex.flex-wrap.align-items-center.BG04.pe-4(style="border-radius: 5px;")
        img(src='@/assets/curso/temas/img02.svg' style="max-width: 32px; margin-left: -16px;")
        h3.col.ps-3.mb-0 Puntos crticos de supervisin (deteccin de riesgos)
    
    p.mb-5 La supervisin debe enfocarse en detectar los siguientes riesgos, que comprometen la salud y la seguridad de la carga:

    TabsC.color-acento-botones.mb-5(data-aos="zoom-out")
      .BG06.px-4.py-3.py-md-4(titulo="Supervisin de la condicin del animal")
        .row.justify-content-center
          .col-lg-4.col-sm-6.col-8.mb-lg-0.mb-4
            img(src='@/assets/curso/tema3/img30.jpg' alt="Imagen decorativa")
          .col-lg-8
            ul.lista-ul--color.mb-0
              li(style="margin-bottom: 2px")
                i.fas.fa-check-circle
                p.mb-0 #[strong Prdida de equilibrio]: supervisar la presencia de animales cados o aquellos que muestren signos de que no pueden permanecer en la posicin de equilibrio esttico (por fatiga o lesin).
              li(style="margin-bottom: 2px")
                i.fas.fa-check-circle
                p.mb-0 #[strong Hostilidad y agresin]: observar signos de peleas o acoso que indiquen que la separacin por grupos (tamao, cuernos, edad) es inadecuada o que las barreras se han movido.
              li(style="margin-bottom: 2px")
                i.fas.fa-check-circle
                p.mb-0 #[strong Signos de estrs]: buscar indicadores de estrs trmico (jadeo, salivacin excesiva) o deshidratacin.
      .BG06.px-4.py-3.py-md-4(titulo="Supervisin del microclima y estructura")
        .row.justify-content-center
          .col-lg-4.col-sm-6.col-8.mb-lg-0.mb-4
            img(src='@/assets/curso/tema3/img31.jpg' alt="Imagen decorativa")
          .col-lg-8
            ul.lista-ul--color.mb-0
              li(style="margin-bottom: 2px")
                i.fas.fa-check-circle
                p.mb-0 #[strong Ventilacin y temperatura]: supervisar activamente que la ventilacin sea constante y que la temperatura se mantenga dentro del rango seguro (debe estar entre 5 C y 30 C).
              li(style="margin-bottom: 2px")
                i.fas.fa-check-circle
                p.mb-0 #[strong Integridad de la separacin]: supervisar que las divisiones o barreras fuertes permanezcan aseguradas y cumplan su funcin de separacin.
              li(style="margin-bottom: 2px")
                i.fas.fa-check-circle
                p.mb-0 #[strong Bioseguridad]: chequear que no haya filtracin de orina y heces a la va pblica.

    .mb-4.d-flex(data-aos="fade-right")
      .d-flex.flex-wrap.align-items-center.BG04.pe-4(style="border-radius: 5px;")
        img(src='@/assets/curso/temas/img02.svg' style="max-width: 32px; margin-left: -16px;")
        h3.col.ps-3.mb-0 Tcnica de supervisin en paradas (accin correctiva)
    
    p.mb-5 Cuando la supervisin revela una necesidad o coincide con una parada de descanso (interrupcin larga), la tcnica se complementa con acciones correctivas:

    .row.justify-content-center.mb-5
      .col-lg-4.col-6.mb-4.mb-lg-0(data-aos="fade-right")
        .crd.crd--grayGrad
          .bottomCircle.mb-5
            figure
              img(src="@/assets/curso/tema3/img32.svg", alt="Imagen decorativa").img90.m-auto
          .row.justify-content-center.mb-4
            .BG07.px-4.py-2
              h4.text-center.mb-0 Acomodacin
          p.text-center.mb-0 Supervisar y corregir el hacinamiento o la posicin incmoda de los animales, asegurando que todos tengan el espacio disponible para levantarse y acostarse.
      .col-lg-4.col-6.mb-4.mb-lg-0(data-aos="flip-up")
        .crd.crd--grayGrad
          .bottomCircle.mb-5
            figure
              img(src="@/assets/curso/tema3/img33.svg", alt="Imagen decorativa").img90.m-auto
          .row.justify-content-center.mb-4
            .BG07.px-4.py-2
              h4.text-center.mb-0 Suministro
          p.text-center.mb-0 Supervisar la hidratacin o alimentacin de los animales en el lugar de descanso si el plan de ruta lo requiere.
      .col-lg-4.col-6.mb-4.mb-lg-0(data-aos="fade-left")
        .crd.crd--grayGrad
          .bottomCircle.mb-5
            figure
              img(src="@/assets/curso/tema3/img34.svg", alt="Imagen decorativa").img90.m-auto
          .row.justify-content-center.mb-4
            .BG07.px-4.py-2
              h4.text-center.mb-0 Contingencia
          p.text-center.mb-0 Si la supervisin detecta un animal enfermo o lesionado, la tcnica de supervisin se transforma inmediatamente en el protocolo del Plan de Contingencia, exigiendo ayuda inmediata y notificacin al ICA.

    .row.justify-content-center.mb-4
      .col-lg-10(data-aos="fade-right")
        .cajon.color-secundario.p-4
          p.mb-0 #[strong Soporte documental (accin correctiva)]: la parada se realiza para inspeccin, acomodacin, hidratacin o alimentacin.  Manual de procedimiento (Num. 4.h)

    separador
    #t_3_7.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 3.7 Tcnicas de control de condiciones trmicas y tipos de riesgos

    p.mb-5 Las tcnicas de control de condiciones trmicas son procedimientos estructurales y operacionales diseados para mantener el microclima dentro de la unidad de transporte en un rango seguro, minimizando el estrs trmico. El incumplimiento de estas tcnicas genera graves riesgos para la salud y la vida de los animales.

    .mb-4.d-flex(data-aos="fade-right")
      .d-flex.flex-wrap.align-items-center.BG04.pe-4(style="border-radius: 5px;")
        img(src='@/assets/curso/temas/img02.svg' style="max-width: 32px; margin-left: -16px;")
        h3.col.ps-3.mb-0 Tcnicas de control de condiciones trmicas

    p El control se centra en asegurar el flujo de aire y proteger a los animales de las condiciones climticas extremas.
    p.mb-5 A continuacin, se presenta la informacin correspondiente a las tcnicas de control trmico.

    .BGIMG01.p-md-5.p-4.mb-5
      .tarjeta.tarjeta--blanca.p-4
        PasosB.color-acento-botones(data-aos="zoom-in")
          .row.justify-content-center.align-items-center
            .col-lg-6.order-lg-1.order-2
              .h4 Control de rango de temperatura
              p.mb-5 La tcnica exige mantener la temperatura dentro del vehculo entre 5C y 30C. Si la temperatura excede este rango, hay falla en el control trmico.
              .h4 Soporte documental (texto exacto)
              p.mb-0 "asegurar la temperatura que no podr ser inferior a 5C ni superior a 30C"  Manual de procedimiento (Num. 4.f)
            .col-lg-6.col-10.order-lg-2.order-1.mb-lg-0.mb-4
              img(src='@/assets/curso/tema3/img35.jpg' alt="Imagen decorativa")
          .row.justify-content-center.align-items-center
            .col-lg-6.order-lg-1.order-2
              .h4 Ventilacin constante
              p.mb-5 Tcnica primordial: asegurar la ventilacin constante en todos los compartimentos. Esto evita la acumulacin de aire caliente (especialmente en la parte superior) y asegura la renovacin de oxgeno y la disipacin de calor y humedad.
              .h4 Soporte documental (texto exacto)
              p.mb-0 "procurar... mantener la ventilacin constante..."  Manual de procedimiento (Num. 4.f)
            .col-lg-6.col-10.order-lg-2.order-1.mb-lg-0.mb-4
              img(src='@/assets/curso/tema3/img36.jpg' alt="Imagen decorativa")
          .row.justify-content-center.align-items-center
            .col-lg-6.order-lg-1.order-2
              .h4 Proteccin estructural (sombra)
              p El vehculo debe contar con techo o cubierta que proteja a los animales de las condiciones climticas adversas, principalmente la radiacin solar directa.
              p.mb-0 "...debern contar con techo o cubierta que proteja a los animales de las condiciones climticas adversas."  Resolucin 20223040006915 (Num. 2.2.c.i)
            .col-lg-6.col-10.order-lg-2.order-1.mb-lg-0.mb-4
              img(src='@/assets/curso/tema3/img37.jpg' alt="Imagen decorativa")
          .row.justify-content-center.align-items-center
            .col-lg-6.order-lg-1.order-2
              .h4 Control de densidad
              p Evitar la sobrecarga o hacinamiento, ya que la proximidad de cuerpos animales aumenta la produccin de calor y humedad, sobrecargando la capacidad del sistema de ventilacin.
              p.mb-0 Implcito en la necesidad de espacio disponible para levantarse y acostarse (Num. 4.e)
            .col-lg-6.col-10.order-lg-2.order-1.mb-lg-0.mb-4
              img(src='@/assets/curso/tema3/img38.jpg' alt="Imagen decorativa")

    .mb-4.d-flex(data-aos="fade-right")
      .d-flex.flex-wrap.align-items-center.BG04.pe-4(style="border-radius: 5px;")
        img(src='@/assets/curso/temas/img02.svg' style="max-width: 32px; margin-left: -16px;")
        h3.col.ps-3.mb-0 Tipos de riesgos derivados de la falla del control trmico
    
    p.mb-5 La falla en el control de las condiciones trmicas expone a los animales a dos tipos de riesgos principales, asociados al sobrecalentamiento o al enfriamiento excesivo.

    .row.justify-content-center.mb-4
      .col-lg-10
        .row.mb-4
          .col-auto.BG11.px-4.py-2(data-aos="fade-right")
            .h4.text-center.mb-0 A.	Riesgos por hipertermia (exceso de calor, superior a 30C)
          
        p.mb-5 A continuacin, se presenta la informacin correspondiente a estos riesgos.

        .titulo-sexto.color-acento-contenido(data-aos="fade-right")
          h5 Tabla 20.
          i Riesgos por hipertermia
        .tabla-a.color-acento-contenido.mb-5
          table(data-aos="zoom-in"  alt="Texto alternativo tabla")
            thead(style="border-color: transparent")
              tr(data-aos="fade-right")
                th(style="background-color: #F3BC5B; width: 24%;") Tipo de riesgo
                th(style="background-color: #F3BC5B; width: 54%;") Consecuencia en el animal
                th(style="background-color: #F3BC5B; width: 24%;") Relacin con la falla
            tbody
              tr(data-aos="fade-right")
                th(style="background-color: #F6F6F6;") Golpe de calor o estrs por calor
                td(style="background-color: #F6F6F6;") Falla en los mecanismos de termorregulacin, jadeo excesivo, salivacin, taquicardia y, en casos graves, colapso y muerte sbita.
                td(style="background-color: #F6F6F6;") Falla de la ventilacin constante o falta de sombra (proteccin).
              tr(data-aos="fade-right")
                th Deshidratacin acelerada
                td Aumento de la evaporacin y la necesidad de agua. El riesgo se incrementa si el viaje es largo y no se suministra hidratacin a tiempo.
                td Falla del control de la humedad y la temperatura.

        .row.mb-5
          .col-auto.BG11.px-4.py-2(data-aos="fade-right")
            .h4.text-center.mb-0 B.	Riesgos por hipotermia (temperaturas inferiores a 5C)
        
        p.mb-5 A continuacin, se presenta la informacin correspondiente a estos riesgos.

        .titulo-sexto.color-acento-contenido(data-aos="fade-right")
          h5 Tabla 21.
          i Riesgos por hipotermia
        .tabla-a.color-acento-contenido.mb-5
          table(data-aos="zoom-in"  alt="Texto alternativo tabla")
            thead(style="border-color: transparent")
              tr(data-aos="fade-right")
                th(style="background-color: #F3BC5B; width: 24%;") Tipo de riesgo
                th(style="background-color: #F3BC5B; width: 54%;") Consecuencia en el animal
                th(style="background-color: #F3BC5B; width: 24%;") Relacin con la falla
            tbody
              tr(data-aos="fade-right")
                th(style="background-color: #F6F6F6;") Congelacin o estrs por fro
                td(style="background-color: #F6F6F6;") Prdida excesiva de calor corporal, temblores, debilidad y enfermedades respiratorias.
                td(style="background-color: #F6F6F6;") Falla en el aislamiento o ventilacin excesiva en climas fros.

        .cajon.color-primario.p-4
          p.mb-0 #[strong Nota]: la deteccin de estos riesgos es una obligacin del conductor o tripulante durante la inspeccin cada cuatro (4) horas. Si se detecta un riesgo, se debe aplicar inmediatamente el Plan de Contingencia.

    separador
    #t_3_8.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 3.8 Tcnicas de aspersin a animales en pie
    
    p.mb-5 Las tcnicas de aspersin se refieren al uso de mtodos de rociado o pulverizacin de agua sobre los animales en pie durante el transporte para ayudar a controlar el estrs trmico (hipertermia). Esta tcnica es un mtodo de emergencia o de manejo en climas clidos para mitigar el riesgo de golpe de calor, especialmente cuando la temperatura supera los (debe estar entre 5C y 30C).

    .row.justify-content-center.mb-5
      .col-lg-3.col-6.mb-lg-0.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/tema3/img39.svg' alt="Imagen decorativa")
      .col-lg-9(data-aos="fade-left")
        .mb-4.d-flex
          .d-flex.flex-wrap.align-items-center.BG04.pe-4(style="border-radius: 5px;")
            img(src='@/assets/curso/temas/img02.svg' style="max-width: 32px; margin-left: -16px;")
            h3.col.ps-3.mb-0 Concepto y finalidad de la aspersin
        p.mb-5 La aspersin no es un requisito de manejo de rutina, sino una medida de mitigacin del riesgo trmico.
        ul.lista-ul--color
          li(style="margin-bottom: 2px")
            i.fas.fa-horse-head
            p.mb-0 #[strong Finalidad primordial]: la tcnica se utiliza para reducir la temperatura corporal del animal a travs del enfriamiento por evaporacin. El agua rociada sobre la piel se evapora, eliminando calor del cuerpo del animal.
          li(style="margin-bottom: 2px")
            i.fas.fa-horse-head
            p.mb-0 #[strong Contexto de aplicacin]: se aplica tpicamente durante las paradas largas o en situaciones de emergencia donde se detectan signos de estrs por calor (jadeo intenso, salivacin, debilidad) y la ventilacin constante no es suficiente.

    .mb-4.d-flex(data-aos="fade-right")
      .d-flex.flex-wrap.align-items-center.BG04.pe-4(style="border-radius: 5px;")
        img(src='@/assets/curso/temas/img02.svg' style="max-width: 32px; margin-left: -16px;")
        h3.col.ps-3.mb-0 Mtodos de aspersin

    p.mb-5 Los mtodos deben ser eficientes para cubrir la mayor rea de la carga sin causar pnico o un exceso de humedad que, en climas templados o fros, podra generar el efecto contrario (hipotermia). A continuacin, se presentan los mtodos, la descripcin y el riesgo a considerar para cada uno.

    .titulo-sexto.color-acento-contenido(data-aos="fade-right")
      h5 Tabla 22.
      i Mtodos de aspersin
    .tabla-a.color-acento-contenido.mb-5
      table(data-aos="zoom-in"  alt="Texto alternativo tabla")
        thead(style="border-color: transparent")
          tr(data-aos="fade-right")
            th(style="background-color: #F3BC5B; width: 24%;") Tipo de mtodo
            th(style="background-color: #F3BC5B; width: 38%;") Descripcin de la tcnica
            th(style="background-color: #F3BC5B; width: 38%;") Riesgo a considerar
        tbody
          tr(data-aos="fade-right")
            th(style="background-color: #F6F6F6;") Aspersin fina (pulverizacin)
            td(style="background-color: #F6F6F6;") Consiste en rociar gotas de agua muy finas sobre el cuerpo del animal. Este es el mtodo preferido porque maximiza la superficie de evaporacin y minimiza el exceso de humedad y el pnico.
            td(style="background-color: #F6F6F6;") Humedad excesiva: si el clima es muy hmedo, la evaporacin es lenta y puede aumentar la humedad en el camin, empeorando el estrs trmico.
          tr(data-aos="fade-right")
            th Aspersin gruesa (riego)
            td Aplicacin de un chorro de agua ms grueso, generalmente en estaciones de lavado o puntos de descanso. Es menos eficiente para el enfriamiento por evaporacin y puede mojar en exceso al animal.
            td Efecto fro/humedad: en climas templados o fros, mojar demasiado al animal sin ventilacin puede provocar hipotermia.
          tr(data-aos="fade-right")
            th(style="background-color: #F6F6F6;") Aspersin automatizada
            td(style="background-color: #F6F6F6;") Uso de sistemas instalados en el vehculo o en las estaciones de descanso que rocan intermitentemente.
            td(style="background-color: #F6F6F6;") Gasto de agua: requiere un monitoreo constante para evitar el desperdicio de agua y la saturacin del lecho del vehculo con humedad.

    .mb-4.d-flex(data-aos="fade-right")
      .d-flex.flex-wrap.align-items-center.BG04.pe-4(style="border-radius: 5px;")
        img(src='@/assets/curso/temas/img02.svg' style="max-width: 32px; margin-left: -16px;")
        h3.col.ps-3.mb-0 Consideraciones tcnicas y normativas

    p.mb-5 Aunque la normativa no menciona la aspersin explcitamente como una obligacin de rutina, s exige el control de la temperatura y la ejecucin de acciones en caso de riesgo, lo cual respalda el uso de esta tcnica:

    .row.justify-content-center.mb-4
      .col-lg-10
        PasosA.color-acento-contenido(tipo="n")
          .row.justify-content-center
            .col-lg-7.order-lg-1.order-2(data-aos="fade-right")
              h4 Obligacin de control trmico
              p La tcnica de manejo requiere mantener la temperatura entre (debe estar entre 5C y 30C), si se supera este lmite, la aspersin se convierte en una medida correctiva.
              ul.lista-ul--color.mb-0
                li(style="margin-bottom: 2px")
                  i.fas.fa-check-circle
                  p.mb-0 #[strong Soporte normativo]: "asegurar la temperatura que no podr ser inferior a (debe estar entre 5C y 30C)."  Manual de procedimiento (num. 4.f) 
            .col-lg-5.col-8.order-lg-2.order-1.mb-lg-0.mb-4(data-aos="fade-left")
              img(src='@/assets/curso/tema3/img40.png', alt="Imagen decorativa")
          .row.justify-content-center
            .col-lg-5.col-8.mb-lg-0.mb-4(data-aos="fade-right")
              img(src='@/assets/curso/tema3/img41.png', alt="Imagen decorativa")
            .col-lg-7(data-aos="fade-left")
              h4 Aspersin como contingencia
              p La aspersin es una forma de proporcionar ayuda o atencin inmediata al animal en riesgo por calor, como parte de la ejecucin del plan de contingencia.
              ul.lista-ul--color.mb-0
                li(style="margin-bottom: 2px")
                  i.fas.fa-check-circle
                  p.mb-0 #[strong Soporte normativo]: el plan de contingencia incluye acciones de "ayuda o atencin inmediata al animal" ante situaciones de emergencia.
          .row.justify-content-center
            .col-lg-7.order-lg-1.order-2(data-aos="fade-right")
              h4 Tcnica de ventilacin
              p.mb-0 La aspersin debe ir siempre acompaada de una ventilacin constante para que el agua se evapore y el proceso de enfriamiento sea efectivo, de lo contrario, solo aumenta la humedad y el riesgo. 
            .col-lg-5.col-8.order-lg-2.order-1.mb-lg-0.mb-4(data-aos="fade-left")
              img(src='@/assets/curso/tema3/img42.png', alt="Imagen decorativa")

    separador
    #t_3_9.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 3.9 Animales cados, enfermos, lesionados, atrapados y muertos
    
    .row.justify-content-center.mb-5
      .col-lg-1.col-2.mb-lg-0.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/tema3/img43.svg' alt="Imagen decorativa")
      .col-lg-11.col-10(data-aos="fade-left")
        p.mb-09 El manejo de animales en estado crtico (cados, enfermos, lesionados, atrapados y muertos) se considera una situacin de contingencia o emergencia durante el proceso de transporte. La normativa establece que el conductor y el transportador deben aplicar inmediatamente el plan de contingencia con el objetivo de mitigar el sufrimiento, el dolor y la mortalidad.

    .mb-4.d-flex(data-aos="fade-right")
      .d-flex.flex-wrap.align-items-center.BG04.pe-4(style="border-radius: 5px;")
        img(src='@/assets/curso/temas/img02.svg' style="max-width: 32px; margin-left: -16px;")
        h3.col.ps-3.mb-0 Deteccin y clasificacin (responsabilidad del conductor/tripulante)

    p.mb-5 La deteccin de un animal en estado crtico ocurre durante la inspeccin obligatoria cada cuatro (4) horas en ruta.

    .titulo-sexto.color-acento-contenido(data-aos="fade-right")
      h5 Tabla 23.
      i Deteccin y clasificacin
    .tabla-a.color-acento-contenido.mb-5
      table(data-aos="zoom-in"  alt="Texto alternativo tabla")
        thead(style="border-color: transparent")
          tr(data-aos="fade-right")
            th(style="background-color: #F3BC5B; width: 24%;") Tipo de condicin de emergencia
            th(style="background-color: #F3BC5B; width: 38%;") Detalle de la deteccin
            th(style="background-color: #F3BC5B; width: 38%;") Estado de manejo
        tbody
          tr(data-aos="fade-right")
            th(style="background-color: #F6F6F6;") Animal cado o atrapado
            td(style="background-color: #F6F6F6;") Animal que no puede permanecer en la posicin de equilibrio esttico (por fatiga, lesin o aplastamiento), o que est atrapado por la estructura o la carga.
            td(style="background-color: #F6F6F6;") Requiere ayuda o atencin inmediata y acomodacin.
          tr(data-aos="fade-right")
            th Animal enfermo o lesionado
            td Animal que presenta lesiones graves, traumatismos o fracturas, o signos evidentes de enfermedad que le impidan continuar el viaje sin sufrimiento.
            td Requiere ayuda o atencin inmediata y, posiblemente, sacrificio humanitario (eutanasia).
          tr(data-aos="fade-right")
            th(style="background-color: #F6F6F6;") Animal muerto
            td(style="background-color: #F6F6F6;") Animal que no presenta signos vitales.
            td(style="background-color: #F6F6F6;") Requiere disposicin sanitaria y notificacin obligatoria inmediata.

    .mb-4.d-flex(data-aos="fade-right")
      .d-flex.flex-wrap.align-items-center.BG04.pe-4(style="border-radius: 5px;")
        img(src='@/assets/curso/temas/img02.svg' style="max-width: 32px; margin-left: -16px;")
        h3.col.ps-3.mb-0 Protocolo de atencin inmediata (plan de contingencia)

    p.mb-5 Ante la deteccin de cualquiera de estas condiciones, el operador debe aplicar las siguientes acciones.

    AcordionA.color-acento-botones.mb-5(tipo="a" clase-tarjeta="tarjeta BG03" data-aos="zoom-in")
      .row.justify-content-center(titulo="Accin inmediata (prioridad: bienestar)")
        .col-lg-6.order-lg-1.order-2
          p El protocolo comienza con la atencin directa para reducir el sufrimiento:
          ul.lista-ul--color.mb-0
            li(style="margin-bottom: 2px")
              i.fas.fa-check-circle
              p.mb-0 #[strong Detencin segura]: detener el vehculo en un lugar seguro y, si es posible, sombreado.
            li(style="margin-bottom: 2px")
              i.fas.fa-check-circle
              p.mb-0 #[strong Ayuda inmediata]: proporcionar ayuda o atencin inmediata al animal lesionado o enfermo. Esto incluye moverlo a una posicin ms cmoda, separarlo de la carga viva si es viable, o liberarlo si est atrapado.
            li(style="margin-bottom: 2px")
              i.fas.fa-check-circle
              p.mb-0 #[strong Acomodacin]: si el animal est cado pero puede levantarse con ayuda, se debe asistirlo y asegurar que el espacio sea suficiente (acomodacin).
        .col-lg-6.col-10.order-lg-2.order-1.mb-lg-0.mb-4
          figure
            img(src='@/assets/curso/tema3/img44.jpg', alt="Imagen decorativa")
      .row.justify-content-center(titulo="Notificacin y coordinacin (prioridad: legal y sanitaria)")
        .col-lg-6.order-lg-1.order-2
          p El operador tiene la obligacin de notificar a la autoridad competente.
          ul.lista-ul--color.mb-0
            li(style="margin-bottom: 2px")
              i.fas.fa-check-circle
              p.mb-0 #[strong Notificacin al ICA]: informar lo sucedido a la oficina del Instituto Colombiano Agropecuario ICA ms cercana o a las autoridades locales, segn la gravedad de la situacin.
            li(style="margin-bottom: 2px")
              i.fas.fa-check-circle
              p.mb-0 #[strong Propsito]: el ICA evala la situacin, emite directrices sobre el manejo del animal y autoriza la disposicin de cadveres.
            li(style="margin-bottom: 2px")
              i.fas.fa-check-circle
              p.mb-0 #[strong Coordinacin local]: si es un accidente grave, se debe contactar a las autoridades locales (municipal o distrital) para coordinar esfuerzos en pro del bienestar de los animales.
        .col-lg-6.col-10.order-lg-2.order-1.mb-lg-0.mb-4
          figure
            img(src='@/assets/curso/tema3/img45.jpg', alt="Imagen decorativa")

    .row.justify-content-center.mb-5
      .col-lg-10(data-aos="fade-right")
        .cajon.color-secundario.p-4
          p.mb-0 #[strong Soporte documental (atencin y notificacin)]: las medidas van desde la ayuda o atencin inmediata al animal, informar a la Oficina del Instituto Colombiano Agropecuario ICA ms cercana...  ABC Manual de Procedimientos para el Transporte, Manejo y Movilizacin de Animales en Pie (1).pdf, pg. 6.

    .row.justify-content-center.mb-5
      .col-lg-4.col-sm-6.col-8.mb-lg-0.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/tema3/img46.png' alt="Imagen decorativa")
      .col-lg-8(data-aos="fade-left")
        .mb-4.d-flex
          .d-flex.flex-wrap.align-items-center.BG04.pe-4(style="border-radius: 5px;")
            img(src='@/assets/curso/temas/img02.svg' style="max-width: 32px; margin-left: -16px;")
            h3.col.ps-3.mb-0 Disposicin sanitaria (animales muertos o sacrificados)
        p Ante un animal muerto o un animal que requiera sacrificio humanitario (eutanasia) por presentar sufrimiento irreversible (traumatismo severo), el plan de contingencia debe incluir:
        .row.justify-content-center
          .col-lg-6.mb-lg-0.mb-4
            .tarjeta-avatar
              img(src='@/assets/curso/tema3/img47.svg' alt="Imagen decorativa")
              .tarjeta.BG03
                .p-4
                  .h4.text-center Sacrificio humanitario
                  p.mb-0 Si es necesario, debe ser realizado por personal competente y capacitado para minimizar el dolor.
          .col-lg-6.mb-lg-0.mb-4
            .tarjeta-avatar
              img(src='@/assets/curso/tema3/img48.svg' alt="Imagen decorativa")
              .tarjeta.BG03
                .p-4
                  .h4.text-center Disposicin sanitaria
                  p.mb-0 El cadver debe ser manejado de acuerdo con las disposiciones sanitarias establecidas, evitando la contaminacin ambiental en la va pblica o en el lugar de descanso.

    .row.justify-content-center.mb-4
      .col-lg-10(data-aos="fade-right")
        .cajon.color-primario.p-4
          p.mb-0 #[strong Soporte documental (sacrificio y disposicin)]: el plan de contingencia incluye la accin de sacrificio y disposiciones de los animales muertos.  ABC Manual de Procedimientos para el Transporte, Manejo y Movilizacin de Animales en Pie (1).pdf, pg. 6.
    
    separador
    #t_3_10.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 3.10 Tcnica de reporte de animales con novedades
    
    p.mb-5 La tcnica de reporte de animales con novedades es el procedimiento obligatorio de notificacin que se activa inmediatamente despus de que el conductor o tripulante detecta una situacin de emergencia o crtica en la carga viva (animales cados, lesionados, enfermos o muertos) durante el trayecto. Esta tcnica es un componente esencial del plan de contingencia.

    .mb-4.d-flex(data-aos="fade-right")
